<template>
<div id="components-layout-demo-basic">
  <a-layout>
    <a-layout-header>全民 Party 豬頭王 連結產生器</a-layout-header>
    <a-layout-content>
      <br />

      <a-row type="flex" justify="space-around" align="middle">
        <a-col :span="12" >
          <span>時間筆數：{{ times_length }}</span>
          <a-textarea v-model="times" placeholder="請貼上房間時間" auto-size />
        </a-col>
        <a-col :span="12">
          <span>房間筆數： {{ rooms_length }}</span>
          <a-textarea v-model="rooms" placeholder="請貼上房間號碼" auto-size />
        </a-col>
        
      </a-row>
      
      <a-button @click="save_rooms" v-if="rooms" type="primary" block>
        產生
      </a-button>

      <br />
      <br />
    <a-list bordered :data-source="party_nos">
      <a-list-item slot="renderItem" slot-scope="item">
        <a slot="actions"><a-button @click="handle_link(item.room)">🐷 GO</a-button></a>
        <a slot="actions">❌</a>
        <a-list-item-meta :description="item.time">
        <a slot="title">{{ item.room }} </a>
        

        <a-avatar slot="avatar">🚪 </a-avatar>
        </a-list-item-meta>
        
      </a-list-item>
    </a-list>
    <a-button @click="clear_rooms" v-if="party_nos.length > 0" type="danger" block>
        全部清除
      </a-button>
    </a-layout-content>
    <a-layout-footer>Copyright © 2021 音浪空間 ᵞᴸ阿里柏</a-layout-footer>
  </a-layout>  
</div>

</template>

<script>



export default {
  data() {
    return {
      "rooms": '',
      "rooms_array": [],
      "times": '',
      "times_array": [],
      "party_nos": [],
    }
  },
  methods: {
    save_rooms(){

      this.rooms_array = this.rooms.split("\n").filter(Number)
      this.times_array = this.times.split("\n").filter(word => word.length > 0)

      // if ( this.times_array.length != this.rooms_array.length ) {
      //   alert("兩邊數量不一致")
      //   return
      // }

      for (let i = 0; i < this.rooms_array.length; i++) {
        this.party_nos.push(
          {
            "time": this.times_array[i],
            "room": this.rooms_array[i],
          }
        )
      }
      
      this.times = ""
      this.rooms = ""

      localStorage.setItem('party_nos', JSON.stringify(this.party_nos))

    },
    clear_rooms(){
      this.times = ""
      this.rooms = ""
      this.party_nos = []
    },
    handle_link(no){
      var index = this.party_nos.indexOf(no);
      if (index !== -1) {
        this.party_nos.splice(index, 1);
      }
      localStorage.setItem('party_nos', JSON.stringify(this.party_nos))

      window.location.href = "https://party.haochang.tv/share/rooms?inviteCode=&roomCode="+ no +"&language=zh-TW&roomId=92190&channel=line&userId=23368648";
    }
  },


  mounted() {
    if (localStorage.party_nos) {
      this.party_nos = JSON.parse(localStorage.party_nos)
    }
  },
  computed:{

    times_length(){
      return this.times.split("\n").filter(word => word.length > 0).length
    },

    rooms_length(){
      return this.rooms.split("\n").filter(Number).length
    },

  }
}
</script>

