#components-layout-demo-basic .ant-layout-footer {
  background: #7dbcea;
  color: #fff;
  font-size: 16px;  
}

#componnts-layout-demo-basic, .ant-layout-content {
    margin: 20px;
}

a:visited {
  color: rgb(149, 82, 151);
}
